<template>
  <div v-if="file">
    <nav class="level is-mobile">
      <div class="level-item has-text-centered">
        <div>
          <p class="heading">{{ $t('file.type') }}</p>
          <p class="title is-4">{{ file.extension.toUpperCase() }}</p>
        </div>
      </div>
      <div class="level-item has-text-centered">
        <div>
          <p class="heading">{{ $t('file.size') }}</p>
          <p class="title is-4">{{ file.size | humanBytes }}</p>
        </div>
      </div>
      <div class="level-item has-text-centered">
        <div>
          <p class="heading">{{ $t('file.downloaded') }}</p>
          <p class="title is-4">{{ file.downloadCount }}</p>
        </div>
      </div>
      <div class="level-item has-text-centered">
        <div>
          <p class="heading">{{ $t('file.visited') }}</p>
          <p class="title is-4">{{ file.visitCount }}</p>
        </div>
      </div>
    </nav>

    <hr>
    <h1 class="title is-5 tracking-wide mt-12 text-center">{{ file.name }}</h1>

    <div class="ads mt-10 text-center">
      <Adsense data-ad-client="ca-pub-4679085340013866" data-ad-slot="6511749031"></Adsense>
    </div>

    <div class="text-center" v-show="showDownloadButtons">
      <a
        :href="`${file.url}`"
        class="button is-primary is-rounded mr-4"
        :class="{ 'pointer-events-none': downloaded }"
        target="_blank"
        @click="onFileDownload"
        :disabled="downloaded"
      >{{ $t("file.downloadFile", { num: '1' }) }}</a>
      <a
        :href="`${file.url_s3}`"
        class="button is-rounded is-white"
        :class="{ 'pointer-events-none': downloaded }"
        target="_blank"
        @click="onFileDownload"
        :disabled="downloaded"
      >{{ $t("file.downloadFile", { num: '2' }) }}</a>
    </div>

    <div class="ads text-center">
      <Adsense data-ad-client="ca-pub-4679085340013866" data-ad-slot="6511749031"></Adsense>
    </div>

    <div class="mt-20" v-if="$i18n.locale === 'en'">
      <h4 class="title title--no-sp is-6">
        Alibaba Cloud services revenues are nearing a $4
        billion annual run rate.
      </h4>
      <p>
        The figure and associated growth rates suggests the China-based technology
        giant may eventually leapfrog Google Cloud Platform (GCP) revenues while pursuing
        Amazon Web Services (AWS) and Microsoft Azure for global public cloud dominance.
        Alibaba cloud revenues were US$962 million for the quarter ended December 31, 2018 —
        up a robust 84 percent compared to the corresponding quarter in 2017, the company
